body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 500;
}

:root {
  --blue: #00acdc;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.is-active {
  color: var(--blue);
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__link {
  color: #333;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--blue);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
